
<template>
  <div>
    <Row>
      <Col span="24">
      <Card>

        <p slot="title">
          <Icon type="grid" /> 操作员查询
        </p>
        <Row>
          <Col style="display: flex; justify-content: flex-start;">
          <div>
            名称：
            <Input v-model="searchConName3" icon="ios-close-empty" placeholder="请输入名称" style="width: 200px" @on-click="handleCancel3"></Input>
          </div>
          <div style="margin-left:50px">
            状态：
            <Select v-model="isEnabled" style="width:200px">
              <Option v-for="item in stateList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </div>
          <div>
            <span @click="changePage(1);" style="margin: 0 10px;">
              <Button type="primary" icon="search" class="btn-gray">查询</Button>
            </span>
          </div>
          </Col>
        </Row>
      </Card>

      <Card class="card-after">
        <Row>
          <Col>
          <!-- 操作员详情 Modal框 -->
          <Modal class-name="vertical-center-modal"  :mask-closable="false" v-model="modal3" ok-text="提交" cancel-text="关闭" :closable="false" width="655">
            <p slot="header" style="color:#484f5f;text-align:left">
              <Icon type="navicon-round" />
              <span>操作员详情</span>
            </p>
            <Row type="flex" justify="center" align="middle" :gutter="16" class="">
              <Col span="4" style="text-align: right">操作员ID：</Col>
              <Col span="8">
              <Input disabled v-model="oldId"></Input>
              </Col>
              <Col span="4" style="text-align: right">操作员名称：</Col>
              <Col span="8">
              <Input disabled v-model="oldName"></Input>
              </Col>
            </Row>
            <br>

            <Row type="flex" justify="center" align="middle" :gutter="16">
              <Col span="4" style="text-align: right">角色名称：</Col>
              <Col span="8">

              <div class="oldRoleStringBox">{{oldRoleString}}</div>
              </Col>
              <Col span="4" style="text-align: right">"状态："</Col>
              <Col span="8">
              <Input disabled v-model="oldState"></Input>
              </Col>
            </Row>
            <br>

            <Row type="flex" justify="center" align="middle" :gutter="16">
              <Col span="4" style="text-align: right">手机：</Col>
              <Col span="8">
              <Input disabled v-model="formModification.newPhone"></Input>
              </Col>
              <Col span="4" style="text-align: right">邮箱：</Col>
              <Col span="8">
              <Input disabled v-model="formModification.newEmail"></Input>
              </Col>
            </Row>
            <br>

            <Row type="flex" justify="center" align="middle" :gutter="16">
              <Col span="4" style="text-align: right">创建日期：</Col>
              <Col span="8">
              <Input disabled v-model="oldTime"></Input>
              </Col>
              <Col span="4" style="text-align: right">最后登陆日期：</Col>
              <Col span="8">
              <Input disabled v-model="oldLasttime"></Input>
              </Col>
            </Row>
            <br>

            <Row type="flex" justify="center" align="middle" :gutter="16">
              <Col span="4" style="text-align: right">最后登陆IP：</Col>
              <Col span="8">
              <Input disabled v-model="oldIp"></Input>
              </Col>
              <Col span="4" style="text-align: right"></Col>
              <Col span="8"></Col>
            </Row>

            <div slot="footer">
              <Button type="default" @click="cancelModal">关闭</Button>
            </div>
          </Modal>

          <Modal
            class-name="vertical-center-modal"
            class="relationTree"
            v-model="modal7"
            :closable="false"
            :mask-closable="false">
            <p slot="header">
              <Icon type="key"/>
              数据权限分配
            </p>
            <div style="display: flex; align-items: center; justify-content: flex-start; padding-bottom: 10px">
              <p style="padding-right: 40px; width: 50%; word-wrap:break-word; word-break:normal;">操作员名称：{{ oldName }} </p>
            </div>
            <Scroll>
              <!-- <Tree :data="tree" show-checkbox multiple @on-check-change="checkInfo"/> -->
              <el-tree
                ref="eltree"
                :data="tree"
                node-key="id"
                :default-checked-keys="selectedItemPermissionsElement"
                show-checkbox
                default-expand-all></el-tree>
              <Spin fix v-if="loadingTree"/>
            </Scroll>
            <div slot="footer">
              <Button type="primary" @click="updateInfo1">确定</Button>
              <Button type="default" @click="cancelModal">取消</Button>
            </div>
          </Modal>

          </Col>

          <!--数据表格展示区-->

          <Table border ref="selection" :loading="loading" :columns="columns1" :data="historyData" @on-selection-change="selectobj" />
          <Page class="card-after" style="marginBottom: 50px;text-align:right;" :current="pageNo" :total="pagetotal" @on-change="changePage" @on-page-size-change="changePageSize" show-sizer show-elevator show-total placement='top' />
        </Row>
      </Card>
      </Col>
    </Row>
  </div>
</template>

<script>
import api from '@/api/api'

export default {
  name: 'AdminTable',
  data() {
    const validatePassword = (rule, value, callback) => {
      if (
        this.formCreated.newPassword1 !== this.formCreated.newPasswordAg1
      ) {
        return callback(new Error("输入的两次密码不一致，请重新输入"))
      } else {
        callback()
      }
    }
     const validatePassword1 = (rule, value, callback) => {
     if (
        this.formModification2.newPassword2 !==
        this.formModification2.newPasswordAg2
      ) {
        return callback(new Error("输入的两次密码不一致，请重新输入"))
      } else {
        callback()
      }
    }
    return {
      formCreated: {
        newName1: '',
        newPassword1: '',
        newPasswordAg1: '',
        newPhone1: '',
        newEmail1: ''
      },

      formModification: {
        newPhone: '',
        newEmail: ''
      },
      formModification2: {
        newPassword2: '',
        newPasswordAg2: ''
      },
      ruleValidate: {
        //修改操作员
        newPhone: [
          { required: false, trigger: 'blur' },
          {
            pattern: /^(0|86|17951)?(13[0-9]|15[0-9]|17[0-9]|18[0-9]|14[0-9])[0-9]{8}$/,
            message: this.$t('admin_manage.validPhoneNumber'),
            trigger: 'blur'
          },
          { max: 20, message: "输入字符长度不允许超出20", trigger: 'blur' }
        ],
        newEmail: [
          { required: false, trigger: 'blur' },
          {
            pattern: /^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)$/g,
            message: "请输入有效的邮箱",
            trigger: 'blur'
          },
          {
            max: 200,
            message: "输入字符长度不允许超出200",
            trigger: 'blur'
          }
        ],
      },
      stateList: [
        {
          value: 1,
          label: "已启用"
        },
        {
          value: 0,
          label: "未启用"
        },
        {
          value: 'all',
          label: "全部"
        }
      ],

      show: true,
      res1: [],
      searchConName3: '', // 首页搜索
      searchConName2: '', // 编辑角色搜索
      searchConName1: '', // 新建角色搜索
      searchDescription1: '', // 描述搜索
      searchDescription2: '', // 描述搜索
      columns1: [
        {
          type: 'selection',
          width: 70,
          align: 'center'
        },
        {
          key: 'username',
          title: "操作员名称",
          align: 'center'
          // width: '18%'
        },
        {
          key: 'loginIp',
          title: "最后登陆IP",
          align: 'center'
          // width: '22%'
        },
        {
          key: 'loginDate',
          title: "最后登陆时间",
          align: 'center'
          // width: '21.6%'
        },
        {
          key: 'isEnabled',
          title: "是否启用",
          align: 'center'
          // width: '18%'
        },
        {
          key: 'handle',
          title: "操作",
          align: 'center'
        }
      ],
      columns2: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: "角色名称",
          key: 'name',
          align: 'center'
        },
        {
          title: "角色描述",
          key: 'description',
          align: 'center'
        }
      ],

      isEnabled: '',
      isEnabledBoolean: true,
      // initTable3: [], // 首页中继容器
      // initTable2: [], // 角色中继容器
      roleTable: [],
      roleTable2: [], // 修改  角色新选角色数组

      selectRole: [], // 新建角色选择数组
      selectRoleId: [], // 新建角色选择数组id
      role: [], // 角色列表
      // roleList: [], //角色table的:data

      // model1: '',

      selectarr: [], // 首页多选id集合

      // ajaxHistoryData: [], // 首页假数据
      // ajaxHistoryData2: [], // 角色假数据

      // 初始化信息总条数
      dataCount: 0,
      historyData: [], // 首页table数据绑定
      historyData2: [],
      current: 1,

      modal1: false,
      modal2: false,
      modal3: false,
      modal4: false,
      modal5: false,
      modal6: false,
      modal7: false,

      loadingTree: true,
      tree: [],
      selectedItemPermissionsElement: [],
      notLeaf: [],
      selectedItem:[],
      permissionIds: [],
      permissionNewIds: [],

      oldId: '',
      oldName: '',
      oldRole: [], // oldRole决定显示在 修改页面——操作员详情页 的角色标签
      oldRoleId: [],
      oldState: '',
      oldPassword: '',
      oldPasswordAg: '',
      oldTime: '',
      oldLasttime: '',
      oldIp: '',
      idsList: [],
      userIdsList: [],
      userId: '',

      loading: false,
      loading2: false,
      roleTableloading: false,
      roleSearchbtn: false,
      oldRoleString: '', // 操作员详情页角色显示
      tempID: '',

      // 分页
      pagetotal: 10,
      pageNo: 1,
      pageSize: 10
    }
  },
  created() {
    this.handleSearch1()
    this.rolesJson()
    this.init()
  },
  mounted() {},
  methods: {
    init() {
      /* 渲染自定义组件 */
      let vm = this
      this.columns1.forEach(item => {
        if (item.key === 'handle') {
          item.render = (h, param) => {
            return h('p', [
              h(
                'Button',
                {
                  props: {
                    type: 'text'
                  },
                  style: {
                    marginRight: '0',
                    color: '#11a0f8'
                  },
                  on: {
                    click: function() {
                      // vm.modal3 = true
                      vm.updateInfo(param.row.id, param.row.userId)
                    }
                  }
                },
                "详情"
              ),
              h('Button', {
                props: {
                  type: 'text'
                },
                style: {
                  marginRight: '0',
                  color: '#11a0f8'
                },
                on: {
                  click: function () {
                    // vm.modal7 = true;
                    vm.showModal()
                    vm.updateInfo2(param.row)//获取当行角色信息
                    vm.generalLocationByUser()
                  }
                }
              }, "赋数据权限")
            ])
          }
        }
      })
    },

    // 多选清除
    handleSelectAll(status) {
      this.$refs.selection2.selectAll(status)
    },

    // 新建标签删除
    handleClose1(event, name) {
      const index = this.selectRole.indexOf(name)
      this.selectRole.splice(index, 1)
      this.selectRoleId.splice(index, 1)
    },
    // 修改标签删除
    handleClose2(event, name) {
      const index = this.oldRole.indexOf(name)
      this.oldRole.splice(index, 1)
      this.oldRoleId.splice(index, 1)
    },

    // 搜索逻辑
    search(data, argumentObj) {
      let res = data
      let dataClone = data
      for (let argu in argumentObj) {
        if (argumentObj[argu].length > 0) {
          res = dataClone.filter(d => {
            return d[argu].indexOf(argumentObj[argu]) > -1
          })
          dataClone = res
        }
      }
      return res
    },

    //    首页搜索功能
    handleSearch1() {
      if (this.isEnabled === 1) {
        this.isEnabledBoolean = true
      } else if (this.isEnabled === 0) {
        this.isEnabledBoolean = false
      } else {
        this.isEnabledBoolean = ''
      }
      let param = {
        username: this.searchConName3,
        isEnabled: this.isEnabledBoolean,
        pageNo: this.pageNo,
        pageSize: this.pageSize
      }
      // console.log(param)
      this.loading = true
      api.searchJson(param).then(
        res => {
          this.historyData = res.result
          if (this.historyData.length === 0 && this.pageNo > 1){
            this.pageNo --
            this.handleSearch1()
          }
          // console.log(this.historyData)
          for (const key in this.historyData) {
            if (this.historyData[key].isEnabled === false) {
              this.historyData[key].isEnabled = "未启用"
            } else {
              this.historyData[key].isEnabled = "已启用"
            }
          }
          this.pagetotal = res.count
          this.pageNo = res.pageNo
          this.pageSize = res.pageSize
          this.loading = false
          this.selectarr = []
          this.idsList = []
          this.userIdsList = []
        },
        err => {
          this.loading = false
        }
      )
    },
     // 重置列表与搜索框内容
    handleCancel3() {
      this.searchConName3 = ''
      this.handleSearch1() // 重置首页列表
    },

    /* 每页显示条目数量 对应更新显示 */
    pagesize(pagesize) {
      this.pageSize = pagesize
    },

    // 重置表单
    handleReset(name) {
      this.$refs[name].resetFields()
    },

    // 详情页  修改页 显示的数据获取
    updateInfo(src, src2) {
      let param = {
        id: src,
        userId: src2
      }
      // console.log(param);
      this.loading2 = true
      api.specificJson(param).then(
        res => {
          if (res.status && res.status === '1222') {
            this.handleSearch1()
            return false
          }
          this.modal3 = true
          this.oldRole = []
          this.oldRoleId = []
          this.oldRoleString = ''
          if (res.result.isEnabled === false) {
            res.result.isEnabled = "未启用"
          } else {
            res.result.isEnabled = "已启用"
          }

          this.oldId = res.result.id
          this.oldName = res.result.name

          // this.oldRole = res.data.result.roles; //角色赋值
          for (const key in res.result.roles) {
            this.oldRole.push(res.result.roles[key].roleName)
            this.oldRoleId.push(res.result.roles[key].roleId)
          }
          // this.oldRoleString = this.oldRole + ''
          for (const key in this.oldRole) {
            this.oldRoleString += this.oldRole[key] + ','
          }
          this.oldState = res.result.isEnabled
          this.formModification.newPhone = res.result.phone
          this.formModification.newEmail = res.result.email
          this.oldTime = res.result.createDate
          this.oldLasttime = res.result.loginDate
          this.oldIp = res.result.loginIp
          this.loading2 = false
        },
        err => {
          this.loading2 = false
        }
      )
    },

    //            首页多选
    selectobj(selection) {
      // console.log(selection)
      this.selectarr = []
      this.idsList = []
      this.userIdsList = []
      for (const key in selection) {
        this.selectionObj = {}
        this.selectionObj.id = selection[key].id.toString()
        this.selectionObj.isEnabled = selection[key].isEnabled
        this.selectionObj.userId = selection[key].userId
        this.selectarr.push(this.selectionObj)
        this.idsList.push(selection[key].id.toString())
        this.userIdsList.push(selection[key].userId.toString())
        // this.selectarr.push(selection[key].id.toString())
      }
      // console.log(this.selectarr)
    },

    //            modal框取消事件
    cancelModal() {
      this.modal1 = false
      this.modal2 = false
      this.modal3 = false
      this.modal4 = false
      this.modal5 = false
      this.modal6 = false
      this.modal7 = false
      this.searchConName1 = ''
      this.searchConName2 = ''
      this.searchDescription1 = ''
      this.searchDescription2 = ''
      this.selectRole = []
      this.selectRoleId = []
      this.oldRole = []
    },

    //            创建时间 yyyy-mm-dd hh:mm:ss
    createdTime() {
      var date = new Date()
      var seperator1 = '-'
      var seperator2 = ':'
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      var currentdate =
        date.getFullYear() +
        seperator1 +
        month +
        seperator1 +
        strDate +
        ' ' +
        date.getHours() +
        seperator2 +
        date.getMinutes() +
        seperator2 +
        date.getSeconds()
      return currentdate
    },

    //  让已选数据默认勾选
    checkList() {
      for (const key in this.roleTable) {
        const element = parseInt(this.roleTable[key].id)
        this.$set(this.roleTable[key], '_checked', false)
        for (const key2 in this.selectRoleId) {
          if (element === parseInt(this.selectRoleId[key2])) {
            this.$set(this.roleTable[key], '_checked', true)
          }
        }
      }
    },

    //          让已选数据默认勾选
    checkList2() {
      for (const key in this.roleTable2) {
        const element = parseInt(this.roleTable2[key].id)
        this.$set(this.roleTable2[key], '_checked', false)
        for (const key2 in this.oldRoleId) {
          if (element === parseInt(this.oldRoleId[key2])) {
            this.$set(this.roleTable2[key], '_checked', true)
          }
        }
      }
    },

    //管理员禁用
    stop() {
      // console.log(this.selectarr)
      if (this.selectarr.length < 1) {
        this.$Message.error("请勾选操作员后，再进行操作")
        return false
      }
      for (const key in this.selectarr) {
        if (
          this.selectarr[key].isEnabled === "未启用"
        ) {
          this.$Message.error("当前勾选的操作员包含未启用操作员，不允许禁用操作")
          return false
        }
      }
      let param = {
        ids: this.idsList,
        userIds: this.userIdsList
      }
      // console.log(param)
      api.stopJson(param).then(
        res => {
          if (res.status && res.status === '1222') {
             if ( this.pagetotal - (this.pageNo - 1) * this.pageSize === this.selectarr.length && this.pageNo > 1)
            {
              this.pageNo--
            }
            this.handleSearch1()
            return false
          }
          this.$Message.success("操作员禁用成功")
          this.handleSearch1()
        },
        err => {
          this.handleSearch1()
        }
      )
    },

    //操作员启用
    start() {
      if (this.selectarr.length < 1) {
        this.$Message.error("请勾选操作员后，再进行操作")
        return false
      }
      for (const key in this.selectarr) {
        if (
          this.selectarr[key].isEnabled === "已启用"
        ) {
          this.$Message.error("当前勾选的操作员含有已启用操作员，不允许启用操作")
          return false
        }
      }
      let param = {
        ids: this.idsList,
        userIds: this.userIdsList
      }
      // console.log(param)
      api.startJson(param).then(
        res => {
          if (res.status && res.status === '1222') {
             if ( this.pagetotal - (this.pageNo - 1) * this.pageSize === this.selectarr.length && this.pageNo > 1)
            {
              this.pageNo--
            }
            this.handleSearch1()
            return false
          }
          this.$Message.success("操作员启用成功")
          this.handleSearch1()
        },
        err => {
          this.handleSearch1()
        }
      )
    },

    // 检测纯数值
    validate(obj) {
      var reg = /^[0-9]*$/
      return reg.test(obj)
    },

    // 编辑角色查询
    rolesJson() {
      let param = {
        roleName: this.searchConName2,
        description: this.searchDescription2,
        pageNo: 1,
        pageSize: 100
      }
      // console.log(param)
      //  this.searchConName2 = ''
      this.roleSearchbtn = true
      this.roleTableloading = true
      api.rolesJson(param).then(res => {
        this.roleTable2 = res.result
        this.checkList2()
      })
      setTimeout(() => {
        this.roleSearchbtn = false
        this.roleTableloading = false
      }, 500)
    },

    // 新建角色查询
    rolesJsonNew() {
      let param = {
        roleName: this.searchConName1,
        description: this.searchDescription1,
        pageNo: 1,
        pageSize: 100
      }
      // console.log(param)
      //  this.searchConName2 = ''
      this.roleSearchbtn = true
      this.roleTableloading = true
      api.rolesJson(param).then(res => {
        this.roleTable = res.result
        this.checkList()
      })
      setTimeout(() => {
        this.roleSearchbtn = false
        this.roleTableloading = false
      }, 500)
    },

    // 改变页面条数
    changePageSize(size) {
      this.pageSize = size
      if (this.pageNo === 1) {
        this.handleSearch1()
      }
    },

    // 改变页面
    changePage(Num) {
      this.pageNo = Num
      this.handleSearch1()
    },

    /* Modal框显示 */
    showModal () {
      this.loadingTree = true
      // this.getTheWholeTree()
    },

    /* 获取旧数据 显示 */
    updateInfo2 (src) {
      this.selectedItem = src
      // console.log(this.selectedItem)
      this.oldName = src.username
    },

        /* 获取选择项的所具有数据权限关联地区的菜单 */
    generalLocationByUser () {
      // console.log(this.selectedItem)
      this.loadingTree = true
      let param = {
        'id': this.selectedItem.id,
        'userId': this.selectedItem.userId
      }
      api.generalLocationByUser(param).then((res) => {
        if(res.status && res.status == 1222){
          this.handleSearch1()
          this.modal7 = false
          return false
        }
        this.modal7 = true
        res = res.result
        // console.log(res)
        let tempId = []
        // let tempNewId = []
        if (res.length > 0) {
          for (let i = 0; i < res.length; i++) {
            tempId.push(parseInt(res[i].id))
            // tempNewId.push(res[i].permissionId)
          }
        }
        this.selectedItemPermissions = tempId
        // console.log(this.selectedItemPermissionsElement)
        /* element-ui 结束 */
        // this.selectedItemNewPermissions = tempNewId
        this.getTheWholeTree(res)
      }, err => {
        this.loadingTree = false
        // console.log(err)
        // this.$Message.error('失败' + err.errorMessage)
      })
    },
    /* 获取全部菜单树 */
    getTheWholeTree (selectItem) {
      let param = {
        'name': ''
      }
      api.regionJson(param).then((res) => {
        res = res.result
        // for (var i = 0; i < res.length; i++) {
        //     this.data2[i] = res[i];
        // }
        // console.log(this.data2);
        /**
         * 需要对返回数据进行处理：
         * 1, 修改name=>title permissions=>children
         * 2, 添加expand: true
         * 暂时使用this.data2
         * */
        this.tree = []
        for (var i = 0; i < res.length; i++) {
          this.jsonData(res[i])
        }
        // var temp = this.jsonData(res);
        /* 获取勾选权限树 element-ui使用 */
        this.selectedItemPermissionsElement = []
        for (let item of selectItem) {
          if (this.notLeaf.indexOf(item.id) === -1) {
            this.selectedItemPermissionsElement.push(parseInt(item.id))
          }
        }
        /* 获取勾选权限树 element-ui使用↑ */
        // console.log(res)
        this.tree = res
        this.loadingTree = false
      }, err => {
        this.loadingTree = false
        // console.log(err)
        // this.$Message.error('失败' + err.errorMessage)
      })
    },
    /* 对后台返回数据进行处理 在json数据中添加组件能够识别出来的键值 */
    jsonData (jsontree) {
      if ((typeof jsontree === 'object') && (jsontree.constructor === Object.prototype.constructor)) {
        var array = []
        array.push(jsontree)
      } else {
        array = jsontree
      }
      // console.log(array)
      for (var i = 0; i < array.length; i++) {
        var jn = array[i]
        // var tmp = this.selectedItemPermissions
        // console.log(jn.id)
        // console.log(this.selectedItemPermissions)
        // console.log(tmp)
        // console.log('********************************************************')
        // let tmp = jn.permissionId.toString()
        // console.log(i)
        // console.log(this.selectedItemPermissions.indexOf(jn.permissionId))
        // console.log(jn)
        // console.log(this.selectedItemPermissions.length)
        // if (jn.permissions.length !== 0) {
        //   this.notLeaf.push(jn.id.toString())
        // }
        jn.label = jn.name // 新增title属性 保证Tree组件识别
        jn.children = jn.permissions // 新增children属性 保证Tree组件识别
        jn.expand = true // 新增expand属性 保证菜单展开
        if (jn.children && jn.children.length > 0) {
          this.jsonData(jn.children)
        } else if (jn.parentId === 0) {
          jn.disabled = true
        }
      }
    },
    /* 数据更新 */
    updateInfo1 () {
      // for (var i = this.ajaxHistoryData.length - 1; i >= 0; i--) {
      //     if (this.ajaxHistoryData[i].id === this.tempID) {
      //         this.ajaxHistoryData[i].name = this.oldName;
      //         this.ajaxHistoryData[i].description = this.oldDescription;
      //         /* 创建时间是否需要更新 不更新就注释掉下面一行 */
      //         this.ajaxHistoryData[i].creatDate = this.getNowFormatDate();
      //     }
      // }
      let halfSelected = this.$refs.eltree.getHalfCheckedNodes()
      let fullSelected = this.$refs.eltree.getCheckedNodes()
      /* element-ui添加部分 ↑ */
      this.modal7 = false
      /* 接口对接 */
      // 将选中的菜单包装到 this.permissionIds
      this.permissionIds = []
      this.permissionNewIds = []
      // for (var i = 0; i < this.checkedMenu.length; i++) {
      //   this.permissionIds.push(this.checkedMenu[i].id.toString())
      //   this.permissionNewIds.push(this.checkedMenu[i].permissionId)
      // };
      for (let half of halfSelected) {
        this.permissionIds.push(half.id)
        this.permissionNewIds.push(half.locationId)
      }
      for (let full of fullSelected) {
        this.permissionIds.push(full.id)
        this.permissionNewIds.push(full.locationId)
      }
      let param = {
        'id': this.selectedItem.id,
        'userId': this.selectedItem.userId,
        'locationid': this.permissionIds,
        'boLocationId': this.permissionNewIds
      }
      // console.log(param)
      this.loading = true
      api.generalUserLinkLocation(param).then((res) => {
        this.handleSearch1()
        this.loading = false
        this.$Message.success("赋数据权限成功")
      }, err => {
        this.loading = false
        // console.log(err)
        // this.$Message.error('失败' + err.errorMessage)
      })
    },
  }
}
</script>
